import React, { useCallback, useEffect, useRef, useState } from 'react';
import ReactDOM from 'react-dom';
import './index.css';
//  import from file
import { randomNotes } from './randomNotes.js';
// svg
import { ReactComponent as ScoreSVG } from './score.svg';
// sound
import { 
    Ab1, Ab2, Ab3, Ab4, Ab5, Ab6, Ab7, A1, A2, A3, A4, A5, A6, A7, 
    Bb1, Bb2, Bb3, Bb4, Bb5, Bb6, Bb7, B0, B1, B2, B3, B4, B5, B6, B7,
    C1, C2, C3, C4, C5, C6, C7, C8, Db1, Db2, Db3, Db4, Db5, Db6, Db7,
    D1, D2, D3, D4, D5, D6, D7, Eb1, Eb2, Eb3, Eb4, Eb5, Eb6, Eb7,
    E1, E2, E3, E4, E5, E6, E7, F1, F2, F3, F4, F5, F6, F7,
    Gb1, Gb2, Gb3, Gb4, Gb5, Gb6, Gb7, G1, G2, G3, G4, G5, G6, G7
} from "./importNotes"
// material-ui
import Button from '@material-ui/core/Button'


// ---------

export function Generator() {
    const defaultAudioTags = <div><audio src={ G4 } controls /><audio src={ A4 } controls /><audio src={ F4 } controls /><audio src={ F3 } controls /><audio src={ C4 } controls /></div>


    const defaultNotes = [
        ["G4", "4"], ["A4", "4"], ["F4", "4"], ["F3", "4"], ["C4", "4"]
    ]
    const [score, setScore] = useState({"notes": defaultNotes, "audioTags": defaultAudioTags})


    const audio_var_list = [
        Ab1, Ab2, Ab3, Ab4, Ab5, Ab6, Ab7, A1, A2, A3, A4, A5, A6, A7, 
        Bb1, Bb2, Bb3, Bb4, Bb5, Bb6, Bb7, B0, B1, B2, B3, B4, B5, B6, B7,
        C1, C2, C3, C4, C5, C6, C7, C8, Db1, Db2, Db3, Db4, Db5, Db6, Db7,
        D1, D2, D3, D4, D5, D6, D7, Eb1, Eb2, Eb3, Eb4, Eb5, Eb6, Eb7,
        E1, E2, E3, E4, E5, E6, E7, F1, F2, F3, F4, F5, F6, F7,
        Gb1, Gb2, Gb3, Gb4, Gb5, Gb6, Gb7, G1, G2, G3, G4, G5, G6, G7
    ]


    const notesInput = [
        "C2", "D2", "E2", "F2", "G2", "A2", "B2", "C3"
    ]

    // const layoutNotes = () => {
    //     score.forEach((note) => {

    //     })
    // }

    
    function playfor700ms(audioTag) {
        return new Promise(async (resolve, reject) => {
            await audioTag.play()
            setTimeout(() => {
                audioTag.pause()
                audioTag.currentTime = 0
                resolve();
            }, 700);
        });
    }

    // function sequencer() {
    //     Tone.js
    //     const Gb4 = new Tone.Player(Gb4).toMaster()
    //     const Gb5 = new Tone.Player(Gb5).toMaster()
    //     let index = 0

    //     Tone.Transport.start()

    //     Gb4.start()
    //     Gb5.start()
    // }

    const audio = useRef()
    const playpauseDiv = useRef()

    // const audioTagList = () => {
    //     if (audio.current.querySelectorAll('audio')) {
    //         return audio.current.querySelectorAll('audio')
    //     }
    //     else {
    //         return null
    //     }
    // } 

    let stopAudio = false

    const play = async () => {
        stopAudio = false
        if (audio.current) {
            const audioTags = audio.current.querySelectorAll('audio')
            for(let audioTag of audioTags) {
                if (stopAudio == false) {
                    await playfor700ms(audioTag);
                } else {
                    break
                }
            }
        }
    }

    const pause = () => {
        stopAudio = true
        if (audio.current) {
            const audioTags = audio.current.querySelectorAll('audio')
            for(let audioTag of audioTags) {
                audioTag.pause()
                audioTag.currentTime = 0
            }
        }
    }

    const playpauseButton = () => {
        if (playpauseDiv.current.className == "play-pause play") {
            playpauseDiv.current.innerHTML = <i class="fas fa-play" OnClick={ play() }></i>
            play.pauseDiv.current.className = "play-pause pause" 
        } else {
            playpauseDiv.current.innerHTML = <i class="fas fa-pause" OnClick={ pause() }></i>
            play.pauseDiv.current.className = "play-pause play" 
        }
    }

    useEffect(() => {
        (async () => {
            for(let audioTag of audio.current.querySelectorAll('audio')) {
                if(stopAudio == false) {
                    await playfor700ms(audioTag);
                } else {
                    audioTag.pause()
                    audioTag.currentime = 0
                    stopAudio = false
                    break
                }
            }
        })();


    },[score])



    return (
        <div className="generator">
            <header>
                <div className="menu"></div>
                <div id="account"></div>
            </header>

            <section className="ingredients">
                <div id="bpm-selector" className="ingredient-selector">
                    <div className="ingredient-title">
                        <p>BPM</p>
                    </div>
                    <div className="ingredient-seperator"></div>
                    <div className="ingredient-input">
                        {/* <input id="bpm-input" /> */}
                        <p>90</p>
                    </div>
                </div>
                <div id="key-selector" className="ingredient-selector">
                    <div className="ingredient-title">
                        <p>Key</p>
                    </div>
                    <div className="ingredient-seperator"></div>
                    <div className="ingredient-input">
                        {/* <input id="key-input" /> */}
                        <p>C</p>
                    </div>
                </div>
                <div id="length-selector" className="ingredient-selector">
                    <div className="ingredient-title">
                        <p>Length</p>
                    </div>
                    <div className="ingredient-seperator"></div>
                    <div className="ingredient-input">
                        {/* <input id="length-input" /> */}
                        <p>4</p>
                    </div>
                </div>
                <div id="scale-selector" className="ingredient-selector">
                    <div className="ingredient-title">
                        <p>Scale</p>
                    </div>
                    <div className="ingredient-seperator"></div>
                    <div className="ingredient-input">
                        {/* <input id="scale-input" /> */}
                        <p></p>
                    </div>
                </div>
                <div id="bpm-selector" className="ingredient-selector">
                    <div className="ingredient-title">
                        <p>BPM</p>
                    </div>
                    <div className="ingredient-seperator"></div>
                    <div className="ingredient-input">
                        {/* <input id="bpm-input" /> */}
                        <p></p>
                    </div>
                </div>
                <div id="bpm-selector" className="ingredient-selector">
                    <div className="ingredient-title">
                        <p>BPM</p>
                    </div>
                    <div className="ingredient-seperator"></div>
                    <div className="ingredient-input">
                        {/* <input id="bpm-input" /> */}
                        <p></p>
                    </div>
                </div>
                <div id="bpm-selector" className="ingredient-selector">
                    <div className="ingredient-title">
                        <p>BPM</p>
                    </div>
                    <div className="ingredient-seperator"></div>
                    <div className="ingredient-input">
                        {/* <input id="bpm-input" /> */}
                        <p></p>
                    </div>
                </div>
            </section>

            <section id="seperator"></section>

            <section className="music">
                <div id="create-button-container">
                    <Button variant="contained" color="primary" onClick={ () => setScore(randomNotes(audio_var_list, notesInput)) }>
                    Create
                    </Button>
                </div>

                <div className="play-pause" id="play" onClick={ () => play() }><i class="fas fa-play"></i></div>
                <div className="play-pause" id="pause" onClick={ () => pause() }><i class="fas fa-pause"></i></div>
                <div className="play-pause play" ref={ playpauseDiv } Onclick={ () => playpauseButton()}></div>
                <div id="like"></div>

                <div className="score">
                    <ScoreSVG />
                    <p>{ score["notes"] }</p>
                    {/* <audio controls>
                        <source src="single-notes/Piano-ff-Gb7.aiff" type="audio/aif"></source>
                        Your browser does not support the audio element.
                    </audio> */}
                    <div className="audioTags" ref={ audio }>{ score["audioTags"] }</div>
                </div>
            </section>
        </div>
    )

}