import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
//  import from file
import { Generator } from './Generator'


class App extends React.Component {

  render() {
    return(
      <div className="app">
        <Generator />
      </div>
    )
  }
}

// ========================================

ReactDOM.render(
  <App />,
  document.getElementById('root')
);

